<code>#include&lt;iostream&gt;
using namespace std;

int argmin(int n,int arr[]){	//n
    int min = 0;				//0
    for(int i=1;i&lt;n;++i)
        if(arr[i]&lt;arr[min])		//
            min = i;			//min
    return min;
}

int main (  )
{
    int n;
    cin&gt;&gt;n;
    int a[n+10];
    for(int i=0;i&lt;n;++i)
        cin&gt;&gt;a[i];
    cout&lt;&lt;argmin(n,a)&lt;&lt;endl;
    return 0;
}</code></pre>
