 <pre class="ne-codeblock language-cpp" data-language="cpp" id="BNQMS"><span
style="font-weight:bold;">5.7</span> 
<code>#include&lt;iostream&gt;
using namespace std;
bool remove(int n,int arr[],int pos){	//npos
    if(pos&lt;0 || pos&gt;=n)  return false;
    for(int i=pos;i&lt;n-1;++i)	//
        arr[i]=arr[i+1];		//
    return true;
}
bool insert(int n,int arr[],int pos,int val){//nposval
    if(pos&lt;0)  return false;
    if(pos&gt;n)	pos = n;		//
    for(int i=n-1;i&gt;=pos;--i)	//
        arr[i+1] = arr[i];		//
    arr[pos] = val;     		//
    return true;
}
void print(int n,int arr[]){
    for(int i=0;i&lt;n;++i)
        cout&lt;&lt;arr[i]&lt;&lt;(i&lt;n-1?' ':'\n');//
}
int main (  )
{
    int n,index;
    cin&gt;&gt;n&gt;&gt;index;
    int values[n];
    for(int i=0;i&lt;n;++i)
        cin&gt;&gt;values[i];
    n-=remove(n,values,index);
    print(n,values);
    n+=insert(n,values,index,37);
    print(n,values);
}</code></pre>
