<code>void merge(int nums1[], int m, int nums2[], int n) {
    int p=m--+(--n);
    while(m&gt;=0&amp;&amp;n&gt;=0){		//while(m+1&amp;&amp;n+1)
        nums1[p--] = nums1[m]&gt;nums2[n]?nums1[m--]:nums2[n--];
    }
    while(n&gt;=0){			//while(n+1)
        nums1[p--] = nums2[n--];
    }
}</code></pre>
