    cin&gt;&gt;L;
    bool tree[L+1];
    for(auto &amp;e:tree)    e = 1;
    int left,right;
    while(cin&gt;&gt;left&gt;&gt;right)
        for(int i=left;i&lt;=right;++i)	//0
            tree[i] = 0;
    int sum = 0;
    for(auto e:tree)
        sum += e;
    cout&lt;&lt;sum&lt;&lt;endl;
    return 0;
}</code></pre>
