 <p class="ne-p" id="uaba1a2ec" style="text-indent: 2em">
  <span class="ne-text" style="font-size: 16px">
   n-2f(n-2)n-1f(n-1)f(n)=f(n-1)+f(n-2)
  </span>
 </p>
 <pre class="ne-codeblock language-cpp" data-language="cpp" id="DpG5i"><span
style="font-weight:bold;">5.14</span>  I
<code>#include&lt;iostream&gt;
using namespace std;
long long fib(int n){
    if(n==1||n==2)return n;
    return fib(n-1)+fib(n-2);
}
int main ()
{
    int n;
    cin&gt;&gt;n;
    long long num = fib(n);
    cout&lt;&lt;num&lt;&lt;endl;
    return 0;
}</code></pre>
