<code>#include&lt;iostream&gt;
using namespace std;

int main (  )
{
    int n;
    cin&gt;&gt;n;
    bool cnt[10010]= {0};				//
    int x,min=10000,max=0,ans;
    for(int i=0; i&lt;n; ++i){
        cin&gt;&gt;x;
        if(x&lt;min)   min = x;			//
        if(x&gt;max)   max = x;			//
        cnt[x] = 1;						//x
    }
    for(int i=min+1;i&lt;=max;++i)			//
        ans += (cnt[i]+cnt[i-1]==2);	//2
    cout&lt;&lt;ans&lt;&lt;endl;
    return 0;
}</code></pre>
