<code>#include&lt;iostream&gt;
using namespace std;

int main (  )
{
    int n;
    cin&gt;&gt;n;
    int res = 0;
    bool prime[n+10];
    for(auto &amp;e:prime)  e=true;
    for (int i = 2; i &lt; n; ++i){
        if (prime[i]) {							//i
            ++res;								//1
            for (int j = i+i; j &lt; n; j+=i)		//ifalse
                prime[j] = false;
        }
    }
    cout&lt;&lt;res&lt;&lt;endl;
    return 0;
}</code></pre>
