 <pre class="ne-codeblock language-cpp" data-language="cpp" id="mrjZR"><span
style="font-weight:bold;">5.20</span> 
<code>#include &lt;iostream&gt;
using namespace std;

void bubble_sort(int n,int a[]) {
    for (int i = 0; i &lt; n; i++) {
        //
        for (int j = 0; j + 1 &lt; n - i; j++) {
            //
            if (a[j] &gt; a[j + 1]) {
                int temp = a[j];
                a[j] = a[j + 1];
                a[j + 1] = temp;
            }
        }
        cout&lt;&lt;""&lt;&lt;i+1&lt;&lt;"";
        for(int j = 0; j&lt; n; ++j)
            cout&lt;&lt;a[j]&lt;&lt;' ';
        cout&lt;&lt;endl;
    }
}

int main(){
    int a[] = { 5,1,4,2,8 };
    bubble_sort(sizeof(a)/sizeof(int),a);
    return 0;
}</code></pre>
