 <pre class="ne-codeblock language-cpp" data-language="cpp" id="QTNB9"><span
style="font-weight:bold;">5.21</span> 
<code>#include &lt;iostream&gt;
#include &lt;algorithm&gt;
using namespace std;

bool cmp(int a,int b){
    return a&gt;b;
}

int main()
{
    int n;
    cin &gt;&gt; n;
    int a[n+3];
    for (int i=0;i&lt;n;++i){
        cin &gt;&gt; a[i];
    }
    sort(a,a+n);			//
    //reverse(a,a+n);		//
    for (int i=0;i&lt;n;++i){
        cout &lt;&lt; a[i] &lt;&lt; " ";
    }
    cout&lt;&lt;endl;
    sort(a,a+n,cmp);			//cmp
    for (int i=0;i&lt;n;++i){
        cout &lt;&lt; a[i] &lt;&lt; " ";
    }
    return 0;
}</code></pre>
