<code>#include &lt;iostream&gt;
using namespace std;
int a[1005][1005];
int main ()
{
    int n,m;
    cin&gt;&gt;n&gt;&gt;m;
    for ( int i = 0; i &lt; n; i++ ){
        for ( int j = 0; j &lt; m; j++ ){
            cin&gt;&gt;a[i][j];
        }
    }
    for ( int j = m-1; j &gt;=0; j-- ){
        for ( int i = 0; i &lt; n; i++ ){
            cout&lt;&lt;a[i][j]&lt;&lt;' ';
        }
        cout&lt;&lt;endl;
    }
    return 0;
}</code></pre>
