<code>#include &lt;iostream&gt;
using namespace std;

int main(){
    string s;
    cout&lt;&lt;s.empty()&lt;&lt;endl;
    cout&lt;&lt;s.size()&lt;&lt;'\t'&lt;&lt;s.capacity()&lt;&lt;endl;
    s="abcdefghijklmnopqrstuvwxyz";
    cout&lt;&lt;s.size()&lt;&lt;'\t'&lt;&lt;s.capacity()&lt;&lt;endl;
    s="abcdefghijklmnopqrstuvwxyzabcdefg";
    cout&lt;&lt;s.size()&lt;&lt;'\t'&lt;&lt;s.capacity()&lt;&lt;endl;
    s.resize(20);
    cout&lt;&lt;s.size()&lt;&lt;'\t'&lt;&lt;s.capacity()&lt;&lt;endl;
    s.resize(70);
    cout&lt;&lt;s.size()&lt;&lt;'\t'&lt;&lt;s.capacity()&lt;&lt;endl;
    s.reserve(80);
    cout&lt;&lt;s.size()&lt;&lt;'\t'&lt;&lt;s.capacity()&lt;&lt;endl;
    s.reserve(50);//s.reserve()
    cout&lt;&lt;s.size()&lt;&lt;'\t'&lt;&lt;s.capacity()&lt;&lt;endl;
    return 0;
}</code></pre>
