style="font-weight:bold;">5.30</span> 
<code>string str_remove(string s, char ch)
{
    size_t j=0;
    for(size_t i=0; i&lt;s.size(); i++)
        if(s[i]!=ch)
            s[j++]=s[i];
    s.resize(j);
    return s; 
}</code></pre>
