<code>#include&lt;iostream&gt;
#include&lt;sstream&gt;		//ostringstream
using namespace std;

int main()
{
    //to_string
    cout&lt;&lt;to_string(1234)&lt;&lt;endl;
    cout&lt;&lt;to_string(1234.56)&lt;&lt;endl;
    //sprintfC
    char str[1000];		//CC
    sprintf(str,"%.2f",1.2345);
    //Cstring
    string s=str;		//
    string s1(str);		//
    string s2 {str};	//
    cout&lt;&lt;s&lt;&lt;endl;
    cout&lt;&lt;s1&lt;&lt;endl;
    cout&lt;&lt;s2&lt;&lt;endl;
    ostringstream oss;
    oss&lt;&lt;3.14;
    oss&lt;&lt;" ";
    oss&lt;&lt;55555555;
    cout&lt;&lt;oss.str();
    return 0;
}</code></pre>
