<code>#include &lt;iostream&gt;
#include &lt;sstream&gt;
using namespace std;
int main()
{
    string str = "good good study day day up";
    istringstream in(str);						//
// 1
    string s;
    while (in &gt;&gt; s){
        cout&lt;&lt;s&lt;&lt;' ';
    }
    cout&lt;&lt;endl;

    str = "good,good,study,day,day,up";
    istringstream in2(str);						//
// 2getline
    while (getline(in2, s, ',')){  	//3
        cout&lt;&lt;s&lt;&lt;' ';
    }
}</code></pre>
