<code>#include &lt;iostream&gt;
using namespace std;
void print(const string&amp; s){
    if(s.size()==1||s.size()&gt;9)
        cout&lt;&lt;s;
    else
        cout&lt;&lt;s[0]&lt;&lt;s.size();
}
int main() {
	string a;
	cin&gt;&gt;a;
	size_t left=0;
    a += '$';
	for(size_t j=1;j&lt;a.size();j++){
        if(a[j]^a[left]){					//if(a[j]!=a[left])
            print(a.substr(left,j-left));
            left = j;
        }
    }
	return 0;
}</code></pre>
